import React from 'react';
import UdIcon from './ud-icon.jsx';
import UdTerminal from './ud-terminal';
import M from 'materialize-css';
import * as clipboard from "clipboard-polyfill"
import {Link} from 'react-router-dom';

export default class UDDesign extends React.Component {

    constructor() {
        super();

        this.state = {
            open : false
        }
    }

    componentDidMount() {
        this.modalInstance = M.Modal.init(this.modal);
    }

    componentWillUnmount() {
        this.modalInstance.destroy();
    }
    
    toggleTerminal() {
        if (this.state.open) {
            this.modalInstance.close();
        } else {
            this.modalInstance.open();
        }
    }

    getFromLS(key) {
        let ls = {};
        if (global.localStorage) {
            try {
            ls = JSON.parse(global.localStorage.getItem(key)) || {};
            } catch (e) {
            /*Ignore*/
            }
        }
        return ls;
    }

    copyLayout() {

        var layout = this.getFromLS("uddesign" + window.location.pathname);
        
        if (layout == null || layout.lg == null) {
            M.toast({html: "No layout saved. Make sure you are using New-UDGridLayout."});
        } 

        layout.lg.forEach(x => {
            x.static = true
        });

        var stringLayout = JSON.stringify(layout);

        clipboard.writeText(stringLayout).then(x => {
            M.toast({ html: "Layout copied to clipboard!"});
        });
    }

    render() {
        return [
            <nav style={{backgroundColor: 'black', color: 'white', height: '30px'}} className="ud-navbar" id="admin-toolbar">
                <ul class="right" style={{lineHeight: '30px'}}>
                    <li><a href="/"><UdIcon icon="Home" /> Home</a></li>
                    <li><a href="/admin/licensing"><UdIcon icon="Key" /> Licensing</a></li>
                    <li><a href="/admin/diagnostics"><UdIcon icon="ChartLine" /> Diagnostics</a></li>
                    <li><a title="Copy Layout" onClick={this.copyLayout.bind(this)}><UdIcon icon="Copy" /> Copy Layout</a></li>
                    <li><a onClick={this.toggleTerminal.bind(this)} id='btnDesignTerminal' title="Open Admin Terminal"><UdIcon icon="Terminal" /> Admin Terminal</a></li>
                </ul>
            </nav>,
            <div ref={modal => this.modal = modal} className="modal">
                <div className="modal-content">
                    <h4><UdIcon icon={'Terminal'}/>  Admin Terminal</h4>
                    <UdTerminal />
                </div>
            </div>
        ]
    }
}