import React from 'react';
import {Redirect} from 'react-router-dom';
import { Card, Table, Col } from 'react-materialize';
import Moment from 'react-moment';
import UdIcon from './ud-icon.jsx';
import Row from 'react-materialize/lib/Row';
import UDDesign from './ud-design.jsx';
import Button from 'react-materialize/lib/Button';

export default class Licensing extends React.Component {

    constructor() {
        super();

        this.state = {
            loading: true
        }
    }

    componentWillMount() {
        UniversalDashboard.get("/license/dashboard", x => {
            this.setState({
                ...x,
                loading: false
            })
        });
    }

    render() {

        if (!UniversalDashboard.adminMode)
        {
            <Redirect to="/" />
        }

        if (this.state.loading) {
            return <div />
        }

        return [
            <UDDesign />,
            <nav style={{backgroundColor: 'black', color: 'white', paddingLeft:'30px'}}>
                <a href="#" className="brand-logo"><UdIcon icon="Key" /> Licensing</a>
            </nav>,
            <main>
                <Row>
                    <Col s={12} l={10} offset={'l1'}>
                        <article>Universal Dashboard is licensed per dashboard. A dashboard is a single Universal Dashboard web server listening on a single port. You can have any number of pages and users connected to a dashboard. </article>
                    </Col>
                </Row>
                <Row>
                    <Col s={12} l={10} offset={'l1'}>
                        <Button onClick={() => window.location.href = 'https://ironmansoftware.com/powershell-universal-dashboard'} style={{width: '300px'}}>Purchase a license</Button>
                    </Col>
                </Row>
                <Row>
                    <Col s={12} l={10} offset={'l1'}>
                        <h5>License</h5>
                        <Card className="ud-card">
                            <Table>
                                <thead>
                                    <tr>
                                        <th>Number of Dashboards</th>
                                        <th>Expiration</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>{this.state.license.seatNumber}</td>
                                        <td><Moment>{this.state.license.endDate}</Moment></td>
                                    </tr>
                                </tbody>
                            </Table>
                        </Card>
                    </Col>
                </Row>
            </main>
        ]
    }
}
