import React from 'react';
import {Route} from 'react-router-dom';
import Licensing from './licensing';
import Diagnostics from './diagnostics';
import UDDesign from './ud-design';
import Login from './login';
import License from './license';
import Watermark from './watermark';
import NoRemote from './no-remote'

const baseRender = UniversalDashboard.renderDashboard;
UniversalDashboard.renderDashboard = (props) => {
    var dashboard = baseRender(props);

    var components = [];
    if (!props.licensed) {
        components.push(<Watermark timeLeft={props.timeLeft}/>)
    }

    if (!props.licensed && !props.isLocal) {
        components.push(<NoRemote />)
    }

    if (UniversalDashboard.adminMode) {
        components.push(<UDDesign />)
    }

    components.push(dashboard);

    return components;
};

UniversalDashboard.registerPlugin({
    provideRoutes: () => {
        return [
            <Route path="/login" component={Login}></Route>,
            <Route path="/signin" component={Login} ></Route>,
            <Route path="/admin/licensing" component={Licensing} ></Route>,
            <Route path="/admin/diagnostics" component={Diagnostics} />
        ]
    }
});