import React from 'react';
import {Redirect} from 'react-router-dom';
import { Button, Card, Row, Col, Table, Collapsible, CollapsibleItem, Collection, CollectionItem } from 'react-materialize';
import UdIcon from './ud-icon.jsx';
import UDDesign from './ud-design.jsx';
import Tabs from './tabs';

const numeral = require('numeral');
const moment = require('moment');

export default class Diagnostics extends React.Component {

    constructor() 
    {
        super();

        this.state = {
            loading: true
        }
    }

    componentWillMount() {
        UniversalDashboard.get("/api/internal/diagnostics", x => {
            this.setState({
                ...x,
                loading: false
            })
        });
    }

    render() {
        
        if (!UniversalDashboard.adminMode)
        {
            <Redirect to="/" />
        }

        if (this.state.loading) {
            return <div />
        }

        var sessions = !this.state.sessions.map ? [] : this.state.sessions.map(session => {
            var sessionVariables = !session.variables ? [] : session.variables.map(variable => {
                return (
                    <tr>
                        <td>{variable.name}</td>
                        <td>{variable.value}</td>
                    </tr>
                )
            })

            var sessionEndpoints = !session.endpoints ? [] : session.endpoints.map(endpoint => {
                var variables = !endpoint.variables ? [] : endpoint.variables.map(variable => {
                    return (
                        <tr>
                            <td>{variable.name}</td>
                            <td>{variable.value}</td>
                        </tr>
                    )
                })
    
                return <EndpointItem variables={variables} id={endpoint.id} scriptText={endpoint.scriptText} averageExecutionTime={endpoint.averageExecutionTime} />
            })

            var endpoints = null;
            if (sessionEndpoints.length > 0)
            {
                endpoints = [ 
                    <h5>Endpoints</h5>,
                    <Collapsible>
                        {sessionEndpoints}
                    </Collapsible>
                ]
            }

            var variables = null;
            if (sessionVariables.length > 0)
            {
                variables = [
                    <h5>Variables</h5>,
                    <Table>
                        <thead>
                            <tr>
                                <th>Name</th>
                                <th>Value</th>
                            </tr>
                        </thead>
                        <tbody>
                            {sessionVariables}
                        </tbody>
                    </Table>
                ]
            }

            var authorizationPolicies = null;
            if (session.authorizationPolicies.length > 0)
            {
                authorizationPolicies = [
                    <h5>Authorization Policies</h5>,
                    <Collection>
                        {session.authorizationPolicies.map(policy => <CollectionItem>{policy}</CollectionItem>)}
                    </Collection>
                ]
            }

            var roles = null;
            if (session.roles.length > 0)
            {
                roles = [
                    <h5>Roles</h5>,
                    <Collection>
                        {session.roles.map(role => <CollectionItem>{role}</CollectionItem>)}
                    </Collection>
                ]
            }

            var header = session.userName ? `${session.userName} (${session.id})` : session.id;

            return (
                <CollapsibleItem header={header}>
                    <h5>Connections: {session.connections}</h5>
                    <h5>Last Touched: {moment(session.lastTouched).format('llll')}</h5>
                    {endpoints}
                    {variables}
                    {authorizationPolicies}
                    {roles}
                </CollapsibleItem>
            )
        })

        var endpoints = !this.state.endpoints ? [] : this.state.endpoints.map(endpoint => {
            var variables = !endpoint.variables ? [] : endpoint.variables.map(variable => {
                return (
                    <tr>
                        <td>{variable.name}</td>
                        <td>{variable.value}</td>
                    </tr>
                )
            })

            return <EndpointItem variables={variables} id={endpoint.id} scriptText={endpoint.scriptText} averageExecutionTime={endpoint.averageExecutionTime} />
        })

        if (endpoints.length === 0)
        {
            endpoints = [<Card><h4>No endpoints</h4></Card>]
        }

        var cache = !this.state.cache ? [] : this.state.cache.map(variable => {
            return (
                <tr>
                    <td>{variable.name}</td>
                    <td>{variable.value}</td>
                </tr>
            )
        })

        var tabs = []

        tabs.push({
            label: "Stats",
            id: 'stats-tab',
            content: <Card className="ud-card">
            <Table>
                <thead>
                    <tr>
                        <th>Sessions</th>
                        <th>Endpoints</th>
                        <th>Cached Items</th>
                        <th>Runspaces</th>
                        <th>Memory Usage</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>{this.state.sessions.length}</td>
                        <td>{this.state.endpoints.length}</td>
                        <td>{this.state.cache.length}</td>
                        <td>{this.state.runspaces}</td>
                        <td>{numeral(this.state.memoryUsage).format('0b')}</td>
                    </tr>
                </tbody>
            </Table>
        </Card>
        })

        tabs.push({
            label: "Endpoints",
            id: 'endpoints-tab',
            content: <Collapsible>
            {endpoints}
        </Collapsible>
        })

        tabs.push({
            label: "Sessions",
            id: 'sessions-tab',
            content: <Collapsible>
            {sessions}
        </Collapsible>
        })

        tabs.push({
            label: "Cache",
            id: 'cache-tab',
            content: <Card className="ud-card">
            <Table>
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Value</th>
                    </tr>
                </thead>
                <tbody>
                    {cache}
                </tbody>
            </Table>
        </Card>
        })


        return [
            <UDDesign />,
            <nav style={{backgroundColor: 'black', color: 'white', paddingLeft:'30px'}}>
                <a href="#" className="brand-logo" id="diagnostics"><UdIcon icon="ChartLine" /> Diagnostics</a>
            </nav>,
            <main>
                <Row>
                    <Col s={12} l={10} offset={'l1'}>
                        <Tabs tabs={tabs} />
                    </Col>
                </Row>
            </main>
            
            
        ]
    }
}

class EndpointItem extends React.Component {
    render()
    {
        return (
            <CollapsibleItem header={this.props.id + ` (${this.props.averageExecutionTime}ms)`}>
                <pre style={{overflow: 'scroll'}}>{this.props.scriptText}</pre>
                <Table>
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Value</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.props.variables}
                    </tbody>
                </Table>
            </CollapsibleItem>
        )
    }
}
