import React from 'react';
import Terminal from 'terminal-in-react';
import { css } from 'emotion'

const terminal = css(`
  pre {
     background-color: black;
     font-size: 18px;
  }

  span {
      font-size: 18px;
  }

  input[type=text] {
      height: 18px;
      border: 0;
      margin-top: 5px !important;
      color: green !important;
      font-size: 18px;
  }

  input[type=text]:focus {
    height: 18px;
    border: 0;
    margin-top: 5px !important;
    color: green;
    font-size: 18px;
}
`)

export default class UDTerminal extends React.Component {

    constructor() {
        super()

        this.state = {
            executing: false
        }
    }

    render() {
        return (
            <div className={terminal} ref={x => this.terminalContainer = x}>
                <Terminal style={{height:'500px', maxHeight: '500px'}}  commandPassThrough={(cmd, print) => {
                
                var input = this.terminalContainer.getElementsByTagName("input")[0]
                const text = cmd.join(' ');

                input.disabled = true;
                print("Executing...");

                UniversalDashboard.postRaw(`/api/internal/component/terminal`, text, res => {
                    print(res);

                    input.disabled = false;
                    input.focus();
                });
                }} watchConsoleLogging={false} promptSymbol={"PS UD:\\> "} hideTopBar={true} allowTabs={false} startState={'maximised'}/>
            </div>
        )
    }
} 